.cell-grop {
  position: relative;
  margin-bottom: 10px;
}

.cell-grop .cell-group-t {
  position: relative;
  padding: 10px;
}

.cell-grop .cell-group-t .cell-title {
  font-size: 16px;
  color: #8391a5;
}

.cell-grop .cell-group-w {
  font-size: 14px;
  padding: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.cell-grop .cell-group-w.label:active {
  background: #f9f9f9;
}

.cell-grop .cell-group-w a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-decoration: none;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
}

.cell-grop .cell-group-w span:nth-of-type(1) {
  color: #333;
}

.cell-grop .cell-group-w span:nth-of-type(2) {
  color: #666;
}

.cell-grop .cell-group-w::before {
  display: block;
  content: "";
  position: absolute;
  left: inherit;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 100%;
  background: #e4e4e4;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}

.cell-grop .cell-group-w.arrow {
  padding-right: 20px;
}

.cell-grop .cell-group-w.arrow::after {
  height: 10px;
  width: 10px;
  border-top: 1px solid #ababab;
  border-right: 1px solid #ababab;
  display: block;
  content: "";
  position: absolute;
  left: inherit;
  right: 8px;
  bottom: inherit;
  top: 50%;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  margin-top: -5px;
}

.popup-group {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  height: 55%;
}

.popup-group .popup-cont, .popup-group .popup-cont-right, .popup-group .popup-cont-left, .popup-group .popup-cont-top {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  padding: 0 5px;
  background: white;
  position: absolute;
  overflow-y: auto;
}

.popup-group .popup-cont-right {
  height: 100%;
  right: 0%;
  top: 0;
  bottom: 0;
  -webkit-transition: all  1s ease;
  transition: all  1s ease;
}

.popup-group .popup-cont-left {
  height: 100%;
  left: 0;
  top: 0;
  bottom: 0;
}

.popup-group .popup-cont-top {
  width: 100%;
  right: 0;
  top: 0;
  left: 0;
  padding: 20px 5px;
}

.popup-group .popup-cont-bottom {
  width: 100%;
  left: 0;
  right: 0;
  bottom: 0;
  padding-bottom: 0px;
  background: white;
  position: absolute;
  overflow-y: auto;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}

.zx-button {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #bfcbd9;
  color: #1f2d3d;
  margin: 0;
  border-radius: 4px;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.04);
}

.zx-button.size-default {
  padding: 10px 15px;
  font-size: 14px;
}

.zx-button.size-mini {
  padding: 8px 10px;
  font-size: 14px;
}

.zx-button.size-large {
  padding: 12px 26px;
  font-size: 16px;
}

.zx-button.size-small {
  padding: 5px 8px;
  font-size: 12px;
}

.zx-button.zx-button-primary {
  color: #fff;
  background-color: #20a0ff;
  border-color: #20a0ff;
}

.zx-button.zx-button-primary:active, .zx-button.zx-button-primary:hover {
  background: #1d90e6;
  border-color: #1d90e6;
}

.zx-button.zx-button-success {
  color: #fff;
  background-color: #13ce66;
  border-color: #13ce66;
}

.zx-button.zx-button-success:active, .zx-button.zx-button-success:hover {
  background: #16bb60;
  border-color: #16bb60;
}

.zx-button.zx-button-warning {
  color: #fff;
  background-color: #f7ba2a;
  border-color: #f7ba2a;
}

.zx-button.zx-button-warning:active, .zx-button.zx-button-warning:hover {
  background: #f1b11a;
  border-color: #f1b11a;
}

.zx-button.zx-button-danger {
  color: #fff;
  background-color: #ff4949;
  border-color: #ff4949;
}

.zx-button.zx-button-danger:active, .zx-button.zx-button-danger:hover {
  background: #e23e3e;
  border-color: #e23e3e;
}

.zx-button.zx-button-info:active, .zx-button.zx-button-info:hover {
  -webkit-box-shadow: inset 0 0 6px #efefef;
          box-shadow: inset 0 0 6px #efefef;
}

.zx-button.zx-button-text {
  border: none;
  color: #20a0ff;
}

.zx-button.disabled, .zx-button.disabled:hover, .zx-button.disabled:active {
  background: #eef1f6;
  border-color: #d1dbe5;
  color: #d1dbe5;
  cursor: no-drop;
}
